	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Docs Special Interest Group</h1>\n<p>Covers documentation, doc processes, and doc publishing for Kubernetes.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/678394311\">Tuesdays at 17:30 UTC</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=17:30&#x26;tz=UTC\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1Ds87eRiNZeXwRBEbFr6Z7ukjbTow5RQcNZLaSvWWQsE/edit\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/playlist?list=PL69nYSiGNLP3b5hlx0YV7Lo7DtckM84y8\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Zach Corleissen (<strong><a href=\"https://github.com/zacharysarah\">@zacharysarah</a></strong>), Linux Foundation</li>\n<li>Andrew Chen (<strong><a href=\"https://github.com/chenopis\">@chenopis</a></strong>), Google</li>\n<li>Jared Bhatti (<strong><a href=\"https://github.com/jaredbhatti\">@jaredbhatti</a></strong>), Google</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-docs\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-docs\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fdocs\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-docs:</p>\n<ul>\n<li>\n<p><strong>kubernetes-bootcamp</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes-bootcamp/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes-bootcamp/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kubernetes-docs-cn</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes-docs-cn/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes-docs-cn/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>reference-docs</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/reference-docs/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/reference-docs/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>website</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/website/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/website/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-docs-maintainers</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-docs-maintainers\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-docs-maintainers\">link</a></td>\n<td>Documentation Maintainers</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-docs-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-docs-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-docs-pr-reviews\">link</a></td>\n<td>Documentation PR Reviewers</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<h2>Goals</h2>\n<ul>\n<li>Discuss documentation and docs issues for kubernetes.io</li>\n<li>Plan docs releases for kubernetes</li>\n<li>Suggest improvements to user onboarding through better documentation on Kubernetes.io</li>\n<li>Identify and implement ways to get documentation feedback and metrics</li>\n<li>\n<p>Help community contributors get involved in kubernetes documentation</p>\n<!-- END CUSTOM CONTENT -->\n</li>\n</ul>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-docs/"}}

